import axios from "axios"
import React from "react"
import {
  Form,
  Icon,
  Input,
  Button,
} from "antd"
import * as PropTypes from "prop-types"
import { withFormik } from "formik"
import * as Yup from "yup"
import FormErrors from "./FormErrors"
import constants from "./constants"

const formItemLayout = {
//   labelCol: {
//     xs: { span: 24 },
//     sm: { span: 8, offset: 8 },
//   },
//   wrapperCol: {
//     xs: { span: 24 },
//     sm: { span: 8, offset: 8 },
//   },
}

const LoginForm = ({
  handleSubmit,
  handleBlur,
  handleChange,
  touched,
  values,
  errors,
  status,
  // dirty,
  history,
  isSubmitting,
}) => (
  <Form {...formItemLayout} onSubmit={handleSubmit} layout="inline">
    <FormErrors errors={errors} notices={status} />
    <Form.Item
      validateStatus={errors.email && touched.email ? "error" : undefined}
      help={(errors.email && touched.email) ? errors.email : ""}
    >
      <Input
        prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
        name="email"
        placeholder="Email"
        onChange={handleChange}
        onBlur={handleBlur}
        value={values.email}
        disabled={isSubmitting}
      />
    </Form.Item>
    <Form.Item>
      <Button
        type="primary"
        htmlType="submit"
        className="login-form-button"
        loading={isSubmitting}
        disabled={
          isSubmitting
          || !!(errors.email && touched.email)
          || !!(errors.password && touched.password)
        }
      >
        Submit Email
      </Button>
    </Form.Item>
  </Form>
)

const valuesProps = PropTypes.shape({
  email: PropTypes.string,
})

LoginForm.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  touched: PropTypes.shape({
    email: PropTypes.bool,
  }).isRequired,
  values: valuesProps,
  errors: valuesProps,
  notices: valuesProps,
  // dirty: PropTypes.object(),
  isSubmitting: PropTypes.bool.isRequired,
}

LoginForm.defaultProps = {
  values: {
    email: "",
  },
  errors: {
    email: undefined,
  },
}

export default 
withFormik({
    mapPropsToValues: () => ({
      email: "",
    }),
    validationSchema: Yup.object().shape({
      email: Yup.string().email("Invalid email").required("Please enter your email"),
    }),
    // eslint-disable-next-line object-curly-newline
    handleSubmit: async ({ email }, { props, setErrors, setSubmitting, setStatus, resetForm }) => {
      try {
        // const response = await props.doLogin({ variables: { email } })
        const response = await axios.get(constants.SUBMIT_URL, {
            params: {
                email,
            },
        })

        if (response && response.status && response.status === 200) {
            if (response.data.result === "success") {
                console.info("success")
                setStatus('Thank you for submitting your email')
                resetForm() 
                window.localStorage.setItem("submitted", 1)
            } else {
                console.error(response)
                throw new Error(response.data.error || "Generic error try again")
            }
        } else {
          // eslint-disable-next-line no-console
          console.error(response.data)
          throw new Error("Response error")
        }

        // props.data.refetch()
        // props.history.push("/profile")
      } catch (e) {
        let errors = []
        console.error(e)

        setSubmitting(false)
        setErrors({ email: "", form: errors })
      }
    },
})(LoginForm)
