import {
  Alert,
} from "antd"
import React from "react"
// import PropTypes from "prop-types"

const FormErrors = ({ errors, notices }) => (
  <div className="alerts">
    {
      errors && errors.form && errors.form.map((error, i) => (
        <Alert key={i} message={error} type="error" />
      ))
    }
    {
      notices && Array.isArray(notices) && notices.map((notice, i) => (
        <Alert key={i} message={notice} type="success" />
      ))
    }
    {
      notices && notices.success &&
        <Alert message={notices.success} type="success" />
    }
    {
      notices && !Array.isArray(notices) &&
        <Alert message={notices} type="success" />
    }
  </div>
)

// FormErrors.propTypes = {
//   errors: PropTypes.oneOf([
//     PropTypes.arrayOf(PropTypes.string),
//     PropTypes.object,
//   ])
// }

FormErrors.defaultProps = {
  errors: [],
}

export default FormErrors
  