import React from "react"
import logo from "./logo.png"
import './App.css'
import LoginForm from "./Signup"

const accepted = !!window.localStorage.getItem("submitted")

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p className="splash">Simple website builder with domain name for <b>$30</b> per year</p>
      </header>
      <section>
        <h1 className="launch-splash">Launching August 2019</h1>
        <p>Enter your email below to be notified of launch date and receive a discount code</p>
        <div className="signup-form">
          { !accepted ? <LoginForm /> : <div>Email already submitted - thank you.</div> }
        </div>
      </section>
      <footer>
        <p><a href="mailto:contact@pagecog.com">contact@pagecog.com</a></p>
      </footer>
    </div>
  );
}

export default App
